import React from 'react';
// styles
import {NavigationOverlayNav , StyledNavLink} from '../styles/NavigationOverlayStyled';
// assets
import homeLinkImg from '../assets/home-link.jpg'
import aboutLinkImg from '../assets/about-link.jpg'
import projectsLinkImg from '../assets/projects-link.jpg'
import contactLinkImg from '../assets/contact-link.jpg'
// icons
import linkedin from '../assets/icons/linkedin.svg'
import github from '../assets/icons/github.svg'

const NavigationOverlay = (props) => {

    return (
        <NavigationOverlayNav active={props.active} onClick={props.clicked}>
            <StyledNavLink to="/" background={homeLinkImg}>Home.</StyledNavLink>
            <StyledNavLink to="/about" background={aboutLinkImg}>About.</StyledNavLink>
            <StyledNavLink to="/projects" background={projectsLinkImg}>Projects.</StyledNavLink>
            <StyledNavLink to="/contact" background={contactLinkImg}>Contact.</StyledNavLink>
            <div>
                <p>Connect </p>
                <ul>
                    <li><a href="https://github.com/kylemharman" target="_blank" rel="noopener noreferrer"><img src={github} alt="github"></img></a></li>
                    <li><a href="https://www.linkedin.com/in/kyle-harman/" target="_blank" rel="noopener noreferrer"><img src={linkedin} alt="linkedin"></img></a></li>
                </ul>
            </div>
            
        </NavigationOverlayNav>
         
    )
}

export default NavigationOverlay;


